import React, { createContext, useState } from "react"
import { ThemeProvider } from "emotion-theming"
import theme from "../utils/theme"
import { css } from "@emotion/core"
import { StaticQuery, graphql } from "gatsby"
import { rhythm } from "../utils/typography"
import AppBar from "../components/app-bar"

const AppContext = createContext({})
export const Consumer = AppContext.Consumer
const Layout = ({ children }: LayoutProps) => {
  const [state, changeState] = useState({
    matchData: [{ team: 4159, "Number of cubes": 0, "Number of balls": 1 }],
    teams: {
      "114": {
        matchData: [
          { "Team number": "114", Number: 1, Number2: "4" },
          { "Team number": "114", Number: 2, Number2: "4" },
          { "Team number": "114", Number: 2, Number2: "4" },
        ],
        stats: {
          Number: {
            mean: 40,
            standardDeviation: 4,
          },
          Number2: {
            mean: 5,
            standardDeviation: 6,
          },
        },
      },
    },
  })

  return (
    <StaticQuery
      query={graphql`
        query SiteTitleQuery {
          site {
            siteMetadata {
              title
            }
          }
        }
      `}
      render={data => (
        <ThemeProvider theme={theme}>
          <AppContext.Provider value={{ state, changeState }}>
            <AppBar siteTitle={data.site.siteMetadata.title} />
            <div
              css={css`
                padding: ${rhythm(2)};
                padding-top: ${rhythm(1.5)};
              `}
            >
              {children}
            </div>
          </AppContext.Provider>
        </ThemeProvider>
      )}
    />
  )
}
interface LayoutProps {
  children: React.ReactNode
}
export default Layout
